.header {
	padding: 20px 0;

	position: fixed;
	z-index: 50;
	width: 100%;

	background-color: var(--black-color);
}

.header_nav {
	display: flex;
	justify-content: space-between;
	align-items: center;
	gap: 30px;
}

.logo {
	width: 60px;
	height: 60px;

	flex-shrink: 0;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	border-radius: 50%;
	background-color: var(--black-color);
	color: var(--white-color);
	box-shadow: 0 0 10px var(--white-color);
	cursor: pointer;
}

.logo span {
	display: block;
	font-size: 16px;
	font-weight: 500;
	line-height: 16px;
	cursor: pointer;
}

.menu_list {
	flex-grow: 1;
	display: flex;
	justify-content: center;
	align-items: center;
	gap: 50px;
}

.menu_link {
	font-size: 20px;
	letter-spacing: 3px;
	text-decoration: none;

	border: 1px solid transparent;
	color: var(--white-color);

	outline: none;
	transition: color var(--hover-time) ease, border-color var(--hover-time) ease;
}

.menu_link:focus {
	color: var(--brand-color);
}

.menu_link:hover {
	border-bottom-color: var(--brand-color);
}

.cart_wrapper {
	position: relative;
	display: flex;
	align-items: center;
	gap: 25px;
	cursor: pointer;
	outline: none;
}

.cart {

	width: 40px;
	height: 40px;
	color: var(--white-color);

	transition: color var(--hover-time) ease;

}

.cart_value {
	font-size: 28px;

	color: var(--white-color);
	transition: color var(--hover-time) ease;
}

.cart_wrapper:focus .cart,
.cart_wrapper:focus .cart_value {
	color: var(--black-900);
}

.cart_wrapper:hover .cart,
.cart_wrapper:hover .cart_value {

	color: var(--brand-color);
}

.cart_wrapper:active {
	top: 1px;
}